.users {
    margin: 2rem auto;
    width: 90%;
    max-width: 40rem;
  }
  
  .users ul {
    list-style: none;
    padding: 1rem;
  }
  
  .users li {
    border: 2px solid #ccc;
    margin: 0.5rem 0;
    padding: 1rem;
    font-size: 1.8rem;
    border-radius: 10px;
    color: #333;
  }